<template>
  <div>
    <el-input v-model="text" clearable placeholder="请输入要查找的内容">
      <template #append>
        <el-button title="查找">
          <el-icon> <Search /> </el-icon>
        </el-button>
      </template>
    </el-input>
  </div>
</template>

<script setup>
import { Search } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "settings-executables-table",

  computed: {},

  data() {
    return {
      text: "",
    };
  },

  watch: {
    text(value) {
      this.$store.commit("executable/update", { filter: value });
    },
  },
};
</script>

